'use client';

import { useState } from 'react';
import { 
  List, 
  ListItem, 
  ListItemButton, 
  ListItemText, 
  Collapse, 
  Box, 
  Typography,
  Chip,
  Divider,
  useTheme,
  Paper 
} from '@mui/material';
import { ExpandLess, ExpandMore, CheckCircle } from '@mui/icons-material';

const chapters = [
  {
    id: 'arrays',
    title: 'Arrays',
    problems: [
      { id: 'two-sum', title: 'Two Sum', level: 'easy' },
      { id: 'best-time-to-buy', title: 'Best Time to Buy and Sell Stock', level: 'easy' },
      { id: 'product-except-self', title: 'Product of Array Except Self', level: 'medium' },
    ]
  },
  {
    id: 'linked-lists',
    title: 'Linked Lists',
    problems: [
      { id: 'reverse-list', title: 'Reverse a Linked List', level: 'easy' },
      { id: 'detect-cycle', title: 'Linked List Cycle', level: 'medium' },
    ]
  },
  // Add more chapters as needed
];

export default function DSAChapterList({ onSelectChapter, selectedChapter, progress }) {
  const [openChapters, setOpenChapters] = useState({});
  const theme = useTheme();

  const toggleChapter = (chapterId) => {
    setOpenChapters(prev => ({
      ...prev,
      [chapterId]: !prev[chapterId]
    }));
  };

  const getCompletedCount = (chapterId) => {
    const chapterProgress = progress[chapterId] || {};
    return Object.values(chapterProgress).filter(Boolean).length;
  };

  return (
    <Paper elevation={0} sx={{ p: 2, backgroundColor: 'background.paper', borderRadius: 2 }}>
      <Typography variant="h6" sx={{ mb: 2, fontWeight: 600 }}>DSA Topics</Typography>
      <List component="nav" sx={{ width: '100%' }}>
        {chapters.map((chapter) => {
          const isOpen = openChapters[chapter.id] ?? true;
          const completedCount = getCompletedCount(chapter.id);
          const totalProblems = chapter.problems.length;
          
          return (
            <Box key={chapter.id}>
              <ListItem 
                disablePadding 
                secondaryAction={
                  <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    {completedCount > 0 && (
                      <Chip 
                        size="small" 
                        label={`${completedCount}/${totalProblems}`} 
                        color="success" 
                        variant="outlined"
                      />
                    )}
                    <Chip 
                      size="small" 
                      label={chapter.problems.length} 
                      color="primary" 
                      variant="outlined"
                    />
                  </Box>
                }
              >
                <ListItemButton 
                  onClick={() => toggleChapter(chapter.id)}
                  selected={selectedChapter?.id === chapter.id}
                  sx={{ borderRadius: 1 }}
                >
                  <ListItemText 
                    primary={chapter.title} 
                    primaryTypographyProps={{ fontWeight: 600 }}
                  />
                  {isOpen ? <ExpandLess /> : <ExpandMore />}
                </ListItemButton>
              </ListItem>
              
              <Collapse in={isOpen} timeout="auto" unmountOnExit>
                <List component="div" disablePadding>
                  {chapter.problems.map((problem) => (
                    <ListItem 
                      key={problem.id} 
                      disablePadding
                      onClick={() => onSelectChapter({ ...chapter, problem })}
                      sx={{ pl: 4 }}
                    >
                      <ListItemButton
                        selected={selectedChapter?.id === chapter.id && selectedChapter?.problem?.id === problem.id}
                        sx={{ borderRadius: 1 }}
                      >
                        <Box sx={{ 
                          display: 'flex', 
                          alignItems: 'center', 
                          gap: 1,
                          width: '100%'
                        }}>
                          <CheckCircle 
                            fontSize="small" 
                            color={progress[chapter.id]?.[problem.id] ? 'success' : 'disabled'} 
                          />
                          <ListItemText 
                            primary={problem.title} 
                            primaryTypographyProps={{ 
                              variant: 'body2',
                              color: progress[chapter.id]?.[problem.id] ? 'text.secondary' : 'text.primary',
                              sx: {
                                textDecoration: progress[chapter.id]?.[problem.id] ? 'line-through' : 'none'
                              }
                            }} 
                          />
                          <Chip 
                            label={problem.level} 
                            size="small" 
                            color={
                              problem.level === 'easy' ? 'success' : 
                              problem.level === 'medium' ? 'warning' : 'error'
                            }
                            variant="outlined"
                            sx={{ 
                              textTransform: 'capitalize',
                              fontSize: '0.65rem',
                              height: 20
                            }}
                          />
                        </Box>
                      </ListItemButton>
                    </ListItem>
                  ))}
                </List>
              </Collapse>
              <Divider sx={{ my: 1 }} />
            </Box>
          );
        })}
      </List>
    </Paper>
  );
}